.blog {
  padding: 0 0 60px;
}

.blog__title {
  font-size: clamp(1.25rem, 1.143rem + 0.54vw, 1.625rem);
  margin-bottom: 30px;
  line-height: 120%;
  color: var(--black-color);
  text-align: center;
}

.blog__text {
  margin-bottom: 30px;
  font-size: clamp(0.875rem, 0.804rem + 0.36vw, 1.125rem);
  color: var(--grey-color);
  line-height: 128%;
  text-align: center;
}

.blog-posts-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: flex-start;
  margin-bottom: 50px;
}

.blog-post {
  background: #fff;
  border: 1px solid #ddd;
  padding: 15px;
  border-radius: 5px;
  transition: box-shadow 0.3s;
  flex: 1 1 calc(33.333% - 20px);
  box-sizing: border-box;
  max-width: calc(33.333% - 20px);
  transition: .2s ease-in;
}

.blog-post:nth-child(1n) {
  flex-basis: calc(33.333% - 20px);
}

.blog-post:nth-child(2n) {
  flex-basis: calc(50% - 20px);
}

.blog-post:nth-child(3n) {
  flex-basis: 100%;
}

.blog-post:hover {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.post-thumbnail img {
  margin-bottom: 10px;
  width: 100%;
  height: 300px;
  border-radius: 10px;
  object-fit: cover;
}

.post-title {
  font-size: clamp(1rem, 0.893rem + 0.54vw, 1.375rem);
  color: var(--accent-color);
  margin: 10px 0;
}

.post-excerpt {
  font-size: .9em;
  color: var(--grey-color);
  line-height: 120%;
}

.pagination {
  margin: 20px 0;
  text-align: center;
}

.pagination a {
  padding: 8px 16px;
  margin: 0 2px;
  border: 1px solid var(--white-color);
  text-decoration: none;
  color: var(--white-color);
  background-color: var(--black-color);
  transition: .2s ease-in;
}

.pagination a:hover {
  color: var(--black-color);
  background: #f0f0f0;
}

.current {
  padding: 8px 16px;
  margin: 0 2px;
  background-color: var(--accent-color);
  border: 1px solid #fff;
  text-decoration: none;
  color: #fff;
}

@media (max-width: 1200px) {
  .blog-post {
    flex: 1 1 calc(50% - 20px);
    max-width: calc(50% - 20px);
  }
}

@media (max-width: 768px) {
  .blog-post {
    flex: 1 1 100%;
    max-width: 100%;
  }

  .blog-post:nth-child(1n) {
    flex-basis: 100%;
    flex: 1 1 100%;
    max-width: 100%;
  }

  .blog-post:nth-child(2n) {
    flex-basis: 100%;
    flex: 1 1 100%;
    max-width: 100%;
  }

  .blog-post:nth-child(3n) {
    flex-basis: 100%;
    flex: 1 1 100%;
    max-width: 100%;
  }
}

.post-content {
  display: flex;
  gap: 30px;
  margin-bottom: 50px;
}

.post-left-column {
  flex: 1 1 40%;
  max-width: 40%;
}


.post-right-column {
  flex: 1 1 60%;
  max-width: 60%;
}

.related-posts {
  margin-top: 50px;
}

.related-posts-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.related-post {
  flex: 1 1 calc(25% - 20px);
  max-width: calc(25% - 20px);
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 10px;
  transition: box-shadow 0.3s;
}

.wp-block-embed iframe {
  max-width: 100%;
  width: 100%;
}

.related-posts h2 {
  margin-bottom: 10px;
  font-size: clamp(1.375rem, 1.304rem + 0.36vw, 1.625rem);
  color: var(--black-color);
  line-height: 133%
}

.related-post:hover {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: .2s ease-in;
}

.related-post-thumbnail img {
  width: 100%;
  height: 200px;
}

.related-post-title {
  margin-bottom: 10px;
  font-size: 18px;
  color: var(--accent-color);
  line-height: 133%;
}

.single__image img {
  height: 500px;
  width: 100%;
  object-fit: cover;
}

.post-description p {
  margin-bottom: 10px;
  line-height: 120%;
}


.post-description ul>li,
.post-description ol>li {
  position: relative;
  margin-bottom: 10px;
  line-height: 120%;
  padding-left: 40px;
  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 4px;
    width: 10px;
    height: 10px;
    background-color: #B029FF;
    transform: rotate(45deg);
  }
}

.post-description h2 {
  margin-bottom: 10px;
  font-size: 24px;
  line-height: 133%;
}

.post-description h3 {
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: var(--regular-font);
  line-height: 133%;
}

.single__title {
  margin-bottom: 30px;
  font-size: clamp(1.375rem, 1.193rem + 0.91vw, 1.875rem);
  font-weight: var(--medium-font);
  color: var(--black-color);
  line-height: 120%;
}


/* Media Queries */
@media (max-width: 1200px) {
  .post-content {
    flex-wrap: wrap;
  }

  .post-left-column {
    flex: 1 1 100%;
    max-width: 100%;
  }

  .post-right-column {
    flex: 1 1 100%;
    max-width: 100%;
  }

  .related-post {
    flex: 1 1 calc(50% - 20px);
    max-width: calc(50% - 20px);
  }
}

@media (max-width: 768px) {
  .related-post {
    flex: 1 1 100%;
    max-width: 100%;
  }
}
