.reviews {
  background-image: url("../img/reviews/bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;

  background-attachment: fixed;

  &__title {
    margin-bottom: 30px;
    text-align: left;
    color: var(--white-color);
  }

  &__text {
    margin-bottom: 30px;
    line-height: 150%;
    color: var(--light-color);
  }

  &__info {
    display: inline-flex;
    gap: 30px;
    border: 1px solid rgba(250, 250, 250, 0.20);
    border-radius: 5px;
    margin-bottom: 60px;
    padding: 30px;
    background-color: rgba(30, 30, 31, 0.50);
    backdrop-filter: blur(2px);

    &-bar {
      width: 350px;
    }
  }

  &__subtitle {
    margin-bottom: 15px;
    font-size: clamp(1.125rem, 1rem + 0.63vw, 1.563rem);
    font-weight: var(--semibold-font);
    line-height: 140%;
    color: var(--white-color);
  }

  &__counter {
    align-items: center;
    gap: 10px;

    &-icon {
      width: 44px;
      height: 44px;
      fill: #ECC705;
    }

    &-text {
      font-size: clamp(2.188rem, 1.991rem + 0.98vw, 2.875rem);
      font-weight: var(--semibold-font);
      line-height: 110%;
      color: var(--white-color);
    }
  }

  &__slider {
    margin-bottom: 30px;
  }
}

.bar {

  &__list {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  &__item {
    align-items: center;
    justify-content: space-between;
    gap: 10px;
  }

  &__text {
    font-size: 14px;
    line-height: 142%;
    color: var(--white-color);

  }
}

.progress-bar {
  position: relative;
  overflow: hidden;
  border-radius: 5px;
  width: 220px;
  height: 6px;
  background-color: var(--white-color);

}

.progress-bar__fill {
  border-radius: 5px;
  height: 100%;
  width: 0;
  background-color: var(--accent-color);
  transition: width 1s ease-in-out;
}

.reviews__item {
  border-radius: 5px;
  padding: 30px;
  background-color: var(--white-color);
  transition: .2s ease-in;

  &-top {
    margin-bottom: 20px;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
  }

  &-bottom {
    align-items: center;
    justify-content: space-between;
    gap: 10px;
  }

  &-date {
    font-size: 14px;
    font-weight: var(--semibold-font);
    line-height: 140%;
    color: var(--grey-color);
    transition: .2s ease-in;
  }

  &-text {
    margin-bottom: 20px;
    font-size: 14px;
    font-weight: var(--semibold-font);
    line-height: 140%;
    color: var(--black-color);
    transition: .2s ease-in;
  }

  &-image {
    width: 95px;
  }

  &-name {
    font-size: 18px;
    line-height: 144%;
    color: var(--grey-color);
    transition: .2s ease-in;
  }

}

.star__list {
  gap: 10px;

  &-item {
    width: 20px;
    height: 20px;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      fill: #ECC705;
    }
  }
}

.custom-progressbar {
  width: 100% !important;
  height: 1px !important;
  background-color: #4D4E4F !important;
  position: relative;
}

.custom-progress {
  height: 100%;
  border-radius: 1px !important;
  background-color: #fff !important;
}

@media screen and (max-width:1024px) {
  .reviews {
    &__info {
      flex-direction: column;

      &-counter {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
      }
    }

    &__subtitle {
      margin-bottom: 0;
    }
  }
}

@media screen and (max-width:500px) {
  .reviews {
    background-position: 90% 50%;

    &__info {
      margin-bottom: 30px;
      width: 100%;
      padding: 20px;
      gap: 15px;

      &-bar {
        width: 100%;
      }
    }
  }

  .progress-bar {
    width: 140px;
    height: 5px;
  }
}
