.area {
  &__title {
    margin-bottom: 30px;
    text-align: center;
  }

  &__text {
    margin: 0 auto 30px;
    width: 606px;
    max-width: 100%;
    font-size: clamp(0.875rem, 0.839rem + 0.18vw, 1rem);
    line-height: 150%;
    color: var(--grey-color);
    text-align: center;
  }

  &__list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 15px;
    margin: 0 auto 60px;
    width: 933px;
    max-width: 100%;

    &-item {
      display: inline-flex;
      align-items: center;
      gap: 5px;
      padding: 15px 30px;
      border: 1px solid var(--light-grey-color);
      border-radius: 5px;

      svg {
        width: 24px;
        height: 24px;
        fill: var(--accent-color);
      }

      span {
        font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
        font-weight: 600;
        line-height: 144%;
        color: var(--black-color);
      }
    }
  }

  &__massonry {
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
  }

  &__item {
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    min-height: 486px;
    padding: 50px 40px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    &-link {
      align-items: center;
      justify-content: center;
      gap: 10px;

      svg {
        width: 24px;
        height: 24px;
        fill: var(--accent-color);
        transition: var(--transition);
      }

      span {
        font-size: 16px;
        font-weight: 600;
        line-height: 150%;
        color: var(--white-color);
        transition: var(--transition);
      }

      &:focus {
        outline: none;
      }

      @include hover {
        span {
          color: var(--accent-color);
        }

        svg {
          fill: var(--white-color);
        }
      }

      &:active {
        span {
          color: var(--grey-color);
        }

        svg {
          fill: var(--accent-color);
        }
      }
    }

    &-text {
      margin-bottom: 20px;
      font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
      font-weight: 500;
      line-height: 144%;
      text-align: center;
      color: var(--white-color);
    }

    &-title {
      position: relative;
      margin-bottom: 30px;
      font-size: clamp(1.563rem, 1.384rem + 0.89vw, 2.188rem);
      font-weight: 600;
      line-height: 114%;
      text-align: center;
      color: var(--white-color);
      &::before {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: -10px;
        margin: auto;
        width: 200px;
        height: 2px;
        background-color: var(--accent-color);
      }
    }

    &:nth-child(1) {
      background-image: url("../img/areas/1.jpg");
    }

    &:nth-child(2) {
      background-image: url("../img/areas/2.jpg");
    }

    &:nth-child(3) {
      background-image: url("../img/areas/3.jpg");
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      background: rgba(30, 30, 31, 0.50);
      backdrop-filter: blur(2px);
    }
  }

  &__info {
    position: relative;
    z-index: 2;
  }
}

@media screen and (max-width:1024px) {
  .area {
    &__massonry {
      grid-template-columns: repeat(2, 1fr);
      gap: 15px;
    }
  }
}

@media screen and (max-width:767px) {
  .area {
    &__massonry {
      grid-template-columns:1fr;
    }
    &__list {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 10px;
      margin-bottom: 30px;
      &-item {
        padding: 15px;
        justify-content: center;
      }
    }
    &__title,
    &__text {
      margin-bottom: 20px;
    }
  }
}
