.single-hero {
  width: 100%;
  height: 456px;
  background-image: url("../img/hero-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  background-attachment: fixed;
}

.single-service {
  padding: 50px 0 100px;

  &__container {
    gap: 30px;
  }

  &__info {
    width: 758px;
    max-width: 100%;
  }

  &__title {
    margin-bottom: 30px;
    text-align: left;
  }

  &__subtitle {
    margin-bottom: 30px;
    font-size: clamp(1.25rem, 1.161rem + 0.45vw, 1.563rem);
    font-weight: 600;
    line-height: 140%;
  }

  &__text {
    margin-bottom: 15px;
    font-size: 14px;
    line-height: 142%;
    color: var(--grey-color);
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 30px;

    &--decimal {
      list-style: decimal;
      padding-left: 22px;

      .single-service__item {
        padding-left: 0;

        strong {
          color: var(--black-color);
        }

        &::before {
          display: none;
        }
      }
    }
  }

  &__item {
    position: relative;
    padding-left: 22px;
    font-size: 14px;
    line-height: 142%;
    color: var(--grey-color);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 4px;
      width: 10px;
      height: 10px;
      background-color: #B029FF;
      transform: rotate(45deg);
    }
  }

  &__aside {
    border-radius: 5px;
    width: 430px;
    max-width: 100%;
    padding: 50px;
    height: max-content;
    transform: translateY(-100px);
    background-color: var(--white-color);
    transition: var(--transition);

    .form__field {
      margin-bottom: 20px;

      &:last-child {
        display: flex;
        justify-content: center;
      }
    }

    .form__input {
      width: 100%;
    }

    .form__btn {
      width: 350px;
      margin: 20px auto 15px;
    }

    .custom-checkbox {
      margin: auto;
    }
  }
}

.ac {
  margin-top: 0;
  border: transparent;
  border-bottom: 1px solid #E2E2E2;
  padding-bottom: 25px;
  background-color: transparent;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;

  &:not(:last-child) {
    margin-bottom: 25px;
  }
}

.service-accordion {

  &__list {
    margin-bottom: 25px;
  }

  &__top {
    cursor: pointer;
    align-items: center;
    gap: 25px;
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    padding: 10px;
    border: 1px solid var(--grey-color);
    border-radius: 100%;
    transition: all var(--transition);

    svg {
      stroke: var(--grey-color);
      transition: all var(--transition);
    }
  }

  &__subtitle {
    font-size: 16px;
    font-weight: var(--semibold-font);
    line-height: 150%;
    color: #333;
    transition: all var(--transition);
  }

  &__bottom {
    padding-left: 75px;
  }

  &__text {
    font-size: 14px;
    line-height: 142%;
    color: var(--grey-color);
  }

  &__item {
    &.is-active {
      padding-left: 25px;

      .accordion__icon {
        border: transparent;
        background-color: var(--accent-color);

        svg {
          stroke: var(--white-color);
        }
      }
    }
  }
}

.aside {
  &__title {
    margin-bottom: 30px;
    font-size: clamp(1.375rem, 1.143rem + 1.16vw, 2.188rem);
    font-weight: 600;
    line-height: 114%;
    text-align: center;
  }
}

@media screen and (max-width:900px) {
  .single-service {
    padding: 50px 0 30px;
    &__container {
      flex-direction: column;
    }
    &__info {
      width: 100%;
    }
    &__aside {
      padding: 20px;
      width: 100%;
      transform: translateY(0);
    }
  }
  .single-service__aside .form__btn {
    width: 100%;
  }
}
