.popup {
  display: none;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.popup-content {
  position: relative;
  margin: 15% auto;
  padding: 50px 50px 70px;
  width: 80%;
  max-width: 627px;
  border-radius: 3px;
  overflow: hidden;
  transform: scale(0.7);
  background-color: var(--white-color);
  transition: transform 0.3s ease;

  &__title {
    margin-bottom: 10px;
    font-size: clamp(1.25rem, 1.161rem + 0.45vw, 1.563rem);
    line-height: 120%;
    text-transform: uppercase;
    text-align: center;
  }

  &__text {
    margin-bottom: 30px;
    text-align: center;
  }

  .form__input {
    min-width: 100%;
  }

  .form__field {
    margin-bottom: 15px;
  }

}

.popup.show {
  display: block;
  opacity: 1;
}

.popup-content.show {
  transform: scale(1);
}

.close {
  position: relative;
  top: -120px;
  right: -20px;
  color: var(--black-color);
  opacity: .6;
  float: right;
  font-size: 28px;
  transition: .2s ease-in;
}

.close:hover,
.close:focus {
  opacity: 1;
  text-decoration: none;
  cursor: pointer;
}

.error {
  color: red;
  font-size: 12px;
}

.hidden {
  display: none;
}

.invalid-input {
  border: 1px solid red;
}


@media screen and (max-width:767px) {
  .popup-content {
    .form__btn span {
      width: 300px;
    }
  }
}

@media screen and (max-width:500px) {
  .popup-content {
    margin: 50% auto;
    width: 90%;
    padding: 50px 20px 70px;

    .form__btn span {
      width: 100%;
    }

    .close {
      right: 0;
    }
  }
}
