.footer {
  padding-bottom: 50px;
  background-color: var(--white-color);

  &__container {
    align-items: center;
    justify-content: space-between;
    gap: 30px;
  }

  &__logo {
    width: 87px;
    height: 87px;
  }

  &__col {
    align-items: center;
    gap: 30px;
  }

  &__list {
    gap: 30px;
  }

  &__link {
    position: relative;
    display: block;
    color: var(--black-color);
    font-weight: var(--medium-font);
    line-height: 150%;
    transition: var(--transition);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: -9px;
      width: 11px;
      height: 11px;
      background-color: var(--accent-color);
      transform: rotate(0deg) scale(0);
      transition: transform var(--transition);
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 1px;
      width: 100%;
      height: 1px;
      border-radius: 5px;
      background-color: var(--accent-color);
      transform: scale(0);
      transition: transform var(--transition);
    }

    &:focus {
      outline: none;
    }

    @include hover {
      &::before {
        transform: rotate(45deg) scale(1);
      }
    }

    &:active {
      color: var(--accent-color);

      &::before {
        transform: rotate(0deg) scale(1);
      }

      &::after {
        transform: scale(1);
      }
    }
  }

  &__copy {
    gap: 10px;

    &-text {
      font-size: 12px;
      line-height: 150%;
      color: #333;
    }

    &-link {
      font-size: 12px;
      line-height: 150%;
      color: #333;
      transition: var(--transition);

      &:hover {
        color: var(--accent-color);
      }
    }
  }
}

@media screen and (max-width:1024px) {
  .footer {
    &__list {
      gap: 10px;
    }
  }
}

@media screen and (max-width:900px) {
  .footer {
    &__container {
      flex-direction: column;
      align-content: center;
    }

    &__col {
      flex-direction: column;
      align-items: center;
    }
  }
}

@media screen and (max-width:500px) {
  .footer {
    &__list {
      flex-direction: column;
      align-items: center;
    }
  }
}
