.services {
  &__title {
    margin-bottom: 30px;
  }
}

.tabs {
  position: relative;
  z-index: 0;
  display: flex;
  justify-content: space-between;
  overflow-x: auto;
  margin-bottom: 100px;

  &::-webkit-scrollbar {
    display: none;
    /* Убираем скроллбар */
  }
}

.tab {
  cursor: pointer;
  font-size: 14px;
  border: transparent;
  border-bottom: 1px solid #4d4e4f24;
  padding: 15px 30px;
  min-width: 270px;
  font-weight: var(--medium-font);
  line-height: 144%;
  color: var(--grey-color);
  text-align: center;
  transition: all var(--transition);

  &:focus {
    outline: none;
  }

  @include hover {
    border-color: var(--accent-color);
    color: var(--accent-color);
  }

  &:active {
    border: 1px solid var(--accent-color);
  }

  &.active {
    color: var(--accent-color);
    border-bottom: 1px solid var(--accent-color);
  }
}


.tab {
  &__row {
    gap: 62px;
  }

  &__descr {
    max-width: 565px;
  }

  &__title {
    align-items: center;
    gap: 20px;
    margin-bottom: 25px;

    &:focus {
      outline: none;
    }

    @include hover {
      .tab__number {
        border-color: var(--accent-color);
        color: var(--accent-color);
      }

      .tab__name {
        color: var(--accent-color);
      }
    }

    &:active {
      .tab__number {
        border-color: var(--black-color);
        color: var(--black-color);
      }

      .tab__name {
        color: var(--black-color);
      }
    }
  }

  &__number {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--grey-color);
    border-radius: 100%;
    width: 50px;
    height: 50px;
    padding: 5px;
    font-weight: var(--semibold-font);
    line-height: 150%;
    color: var(--grey-color);
    transition: all var(--transition);
  }

  &__name {
    font-size: clamp(1.563rem, 1.384rem + 0.89vw, 2.188rem);
    font-weight: var(--semibold-font);
    line-height: 114%;
    color: var(--grey-color);
    transition: all var(--transition);
  }

  &__text {
    margin-bottom: 30px;
    color: var(--grey-color);
    line-height: 150%;
  }

  &__btn {
    margin-top: 5px;
    width: 350px;
  }

  &__image {
    border-radius: 5px;
  }
}

.tab-content {
  position: relative;
  z-index: 2;
  display: none;
  padding: 20px;
  transition: opacity 0.3s ease;

  &[data-tab].active {
    display: block;
    opacity: 1;
  }
}

.ac {
  margin-top: 0;
  border: transparent;
  border-bottom: 1px solid #E2E2E2;
  padding-bottom: 25px;
  background-color: transparent;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;

  &:not(:last-child) {
    margin-bottom: 25px;
  }
}

.accordion {

  &__list {
    margin-bottom: 25px;
  }

  &__top {
    cursor: pointer;
    align-items: center;
    gap: 25px;
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    padding: 10px;
    border: 1px solid var(--grey-color);
    border-radius: 100%;
    transition: all var(--transition);

    svg {
      width: 20px;
      height: 20px;
      stroke: var(--grey-color);
      transition: all var(--transition);
    }
  }

  &__subtitle {
    font-size: 16px;
    font-weight: var(--semibold-font);
    line-height: 150%;
    color: #333;
    transition: all var(--transition);
  }

  &__bottom {
    padding-left: 75px;
  }

  &__text {
    font-size: 14px;
    line-height: 142%;
    color: var(--grey-color);
  }

  &__item {
    &.is-active {
      padding-left: 25px;

      .accordion__icon {
        border: transparent;
        background-color: var(--accent-color);

        svg {
          stroke: var(--white-color);
        }
      }
    }
  }
}

@media screen and (max-width:1024px) {
  .services {
    padding-top: 0;
  }

  .tabs {
    margin-bottom: 30px;
  }

  .tab {
    &__row {
      flex-direction: column;
      gap: 20px;
    }

    &__descr {
      order: 1;
      max-width: 100%;
    }

    &__image-wrapper {
      order: 2;
    }

    &__btn {
      order: 3;
      width: 100%;
    }

    &-content {
      padding: 0;
    }
  }
}

@media screen and (max-width:500px) {
  .services {
    &__title {
      margin-bottom: 15px;
    }
  }

  .tab {
    min-width: 200px;

    &__text {
      margin-bottom: 20px;
    }

    &__number {
      width: 40px;
      height: 40px;
    }

    &__title {
      margin-bottom: 15px;
    }
  }

  .accordion__item.is-active {
    padding-left: 5px;
  }

  .accordion {
    &__top {
      gap: 15px;
    }

    &__bottom {
      padding-left: 55px;
    }

    &__icon {
      width: 40px;
      height: 40px;

      svg {
        width: 15px;
        height: 15px;
      }
    }
  }

  .ac {
    padding-bottom: 15px;
  }
}
