.contact {
  &__container {
    position: relative;
    max-width: 1440px;
  }

  &__image {
    position: relative;
    z-index: 1;
    left: -10%;
    width: 987px;
    max-width: 100%;
    flex-shrink: 0;
    min-height: 919px;
    background-image: url("../img/contact.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  &__body {
    position: absolute;
    right: max(15px, calc((100% - 1320px) / 2));
    top: 50%;
    z-index: 3;
    border-radius: 5px;
    width: 645px;
    padding: 50px;
    background-color: var(--white-color);
    transform: translateY(-50%);

    .form__field {
      margin-bottom: 20px;

      &:last-child {
        display: flex;
        justify-content: center;
      }
    }

    .form__input {
      width: 100%;
    }

    .form__btn {
      width: 350px;
      margin: 20px auto 15px;
    }

    .custom-checkbox {
      margin: auto;
    }
  }

  &__title {
    margin-bottom: 30px;
    font-size: clamp(1.563rem, 1.384rem + 0.89vw, 2.188rem);
    text-align: center;
  }
}

@media screen and (max-width:1024px) {
  .contact {
    &__container {
      padding: 0;
    }

    &__image {
      left: 0;
      right: 0;
      width: 100%;
    }

    &__body {
      left: 50%;
      right: auto;
      margin: auto;
      transform: translate(-50%, -50%);
    }
  }
}

@media screen and (max-width:767px) {
  .contact {
    &__body {
      top: 50px;
      left: 15px;
      right: 15px;
      margin: auto;
      width: 90%;
      padding: 20px;
      transform: translate(0);
    }

    &__image {
      min-height: 800px;
    }
  }

  .contact__body .form__btn {
    width: 100%;
  }
}
