html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
p,
ul,
ol {
  margin: 0;
  padding: 0;
}

.page {
  overflow-x: hidden;
  height: 100%;
}

.page__body {
  position: relative;
  overflow-x: hidden;
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-family: var(--font-family);
  font-weight: var(--regular-font);
  font-size: 16px;
  background-color: var(--light-color);
}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
  min-height: 100vh;
}

.main {
  position: relative;
  flex: 1 1 auto;
}

.is-hidden {
  display: none !important;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.flex {
  display: flex;
}

.grid {
  display: grid;
}

.centered {
  text-align: center;
}

.page--ios .dis-scroll {
  position: relative;
}

.primary__btn {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: 1px solid transparent;
  border-radius: 5px;
  padding: 20px 25px;
  font-weight: var(--semibold-font);
  line-height: 150%;
  color: var(--white-color);
  text-transform: uppercase;
  background-color: var(--accent-color);
  transition: all var(--transition);

  &:focus {
    outline: none;
  }

  @include hover {
    border-color: var(--accent-color);
    color: var(--accent-color);
    background-color: var(--white-color);
  }

  &:active {
    color: var(--white-color);
    background-color: var(--accent-color);
  }

}


.section {
  padding: 80px 0;

  &__title {
    font-size: clamp(2.188rem, 1.92rem + 1.34vw, 3.125rem);
    font-weight: var(--semibold-font);
    line-height: 120%;
    text-transform: uppercase;
    text-align: center;
    color: var(--black-color);
  }
}

.swiper-button-disabled {

  border-color: var(--light-grey-color) !important;

  svg {
    stroke: var(--light-grey-color) !important;
  }
}

.breadcrumbs {
  align-items: center;
  flex-wrap: wrap;
  gap: 13px;
  margin: 136px 0 47px;

  &__item {
    position: relative;

    &:not(:last-child) {
      padding-right: 19px;

      &::before {
        content: "";
        position: absolute;
        right: -3px;
        top: 50%;
        width: 6px;
        height: 6px;
        background-color: #D9D9D9;
        transform: rotate(-45deg) translateY(-50%);
      }
    }
  }

  &__link {
    position: relative;
    display: block;
    font-size: 12px;
    font-weight: 400;
    line-height: 150%;
    color: #830EC7;
    border-bottom: 1px solid #830EC7;
    transition: var(--transition);

    &--disable {
      pointer-events: none;
      cursor: default;
      border-bottom: none;
      color: #B9B9B9;
    }

    &:hover {
      border-color: var(--black-color);
      color: var(--black-color);
    }
  }
}

.form__btn {
  @include hover {
    border-color: var(--accent-color);
      color: var(--accent-color);
  }

  &:active {
    color: var(--white-color);
    background-color: var(--accent-color);
  }
}

.invalid-text {
  margin-top: 5px;
  color: red;
  font-size: 12px;
}

.invalid-input {
  border: 1px solid red !important;
}

@media screen and (max-width:500px) {
  .primary__btn {
    width: 100%;
  }

  .section {
    padding: 30px 0;
    &__title {
      text-align: left;
    }
  }
}
