.hero {
  position: relative;
  padding-top: 230px;

  &__info {
    margin-bottom: 50px;
    align-items: flex-end;
    gap: 30px;
  }

  &__title {
    max-width: 717px;
    font-size: clamp(2.188rem, 1.92rem + 1.34vw, 3.125rem);
    font-weight: var(--semibold-font);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--black-color);
  }

  &__text {
    max-width: 573px;
    font-size: clamp(0.875rem, 0.839rem + 0.18vw, 1rem);
    line-height: 150%;
    color: var(--grey-color);
  }

  &__image {
    width: 100%;
    height: 675px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__btn {
    position: fixed;
    left: 24px;
    top: 50%;
    z-index: 10;
    display: inline-block;
    border-radius: 5px;
    padding: 15px 20px;
    font-weight: var(--semibold-font);
    line-height: 150%;
    text-transform: uppercase;
    background-color: var(--accent-color);
    transform: rotate(-90deg) translateX(-50%);
    transform-origin: left center;
    color: var(--white-color);
    transition: all var(--transition);

    &:focus {
      outline: none;
    }

    @include hover {
      border-color: var(--accent-color);
      color: var(--accent-color);
      background-color: var(--white-color);
    }

    &:active {
      color: var(--white-color);
      background-color: var(--accent-color);
    }
  }
}

@media screen and (max-width:900px) {
  .hero {
    padding-top: 170px;

    &__info {
      margin-bottom: 30px;
      flex-direction: column;
      align-items: flex-start;
      gap: 15px;
    }

    &__text,
    &__title {
      max-width: 100%;
    }

    &__image {
      height: 470px;
    }
    &__btn {
      display: none;
    }
  }
}


@media screen and (max-width:500px) {
  .hero {
    padding-top: 120px;
  }
}
