.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  padding: 6.5px 0;
  background-color: var(--white-color);
  transition: var(--transition);

  &__body {
    align-items: center;
    gap: 26px;
  }

  &__logo {
    display: block;
    width: 87px;
    height: 87px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__list {
    gap: 30px;
  }

  a {
    position: relative;
    display: block;
    color: var(--black-color);
    font-weight: var(--medium-font);
    line-height: 150%;
    transition: var(--transition);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: -9px;
      width: 11px;
      height: 11px;
      background-color: var(--accent-color);
      transform: rotate(0deg) scale(0);
      transition: transform var(--transition);
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 1px;
      width: 100%;
      height: 1px;
      border-radius: 5px;
      background-color: var(--accent-color);
      transform: scale(0);
      transition: transform var(--transition);
    }

    &:focus {
      outline: none;
    }

    @include hover {
      &::before {
        transform: rotate(45deg) scale(1);
      }
    }

    &:active {
      color: var(--accent-color);

      &::before {
        transform: rotate(0deg) scale(1);
      }

      &::after {
        transform: scale(1);
      }
    }
  }

  &__menu {
    align-items: center;
    justify-content: space-between;
    gap: 15px;
    width: 100%;
  }

  &__contact {
    align-items: center;
    gap: 20px;

    &-link {
      align-items: center;
      gap: 10px;

      span {
        font-weight: var(--semibold-font);
        line-height: 150%;
        color: var(--black-color);
        transition: color var(--transition);
      }

      &:focus {
        outline: none;
      }

      @include hover {
        .header__contact-icon {
          background-color: var(--accent-color);

          svg {
            fill: var(--white-color);
          }
        }

        span {
          color: var(--accent-color);
        }
      }

      &:active {
        span {
          color: var(--grey-color);
        }
      }
    }

    &-icon {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 44px;
      height: 44px;
      border-radius: 100%;
      padding: 10px;
      background-color: var(--light-color);
      transition: var(--transition);

      svg {
        width: 24px;
        height: 24px;
        fill: var(--accent-color);
      }
    }
  }

}

.header.hidden {
  opacity: 0;
  pointer-events: none;
}


.header__link.focus-visible {
  outline: 1px solid rgb(246, 3, 3);
  outline-offset: 3px;
}

.burger {
  position: relative;
  z-index: 1000;
  border-radius: 5px;
  padding: 10px;
  width: 44px;
  height: 44px;
  background-color: var(--accent-color);
  cursor: pointer;

  &__line {
    position: absolute;
    right: 10px;
    top: 50%;
    border-radius: 5px;
    width: 40%;
    height: 2px;
    background-color: var(--white-color);
    transform: translateY(-50%);
    transition: opacity .2s ease-in-out;
  }

  &::before {
    content: "";
    position: absolute;
    right: 10px;
    top: 35%;
    border-radius: 5px;
    width: 50%;
    height: 2px;
    background-color: var(--white-color);
    transition: transform .2s ease-in-out, top .2s ease-in-out;
  }

  &::after {
    content: "";
    position: absolute;
    right: 10px;
    bottom: 35%;
    width: 30%;
    height: 2px;
    border-radius: 5px;
    background-color: var(--white-color);
    transition: transform .2s ease-in-out, bottom .2s ease-in-out;
  }

  &--active {
    .burger__line {
      opacity: 0;
    }

    &::before {
      top: 50%;
      background-color: #fff;
      transform: rotate(45deg);
    }

    &::after {
      top: 50%;
      bottom: auto;
      width: 50%;
      background-color: #fff;
      transform: rotate(-45deg);
    }
  }
}

.mobile {
  &__nav {
    display: none;
    align-items: center;
    gap: 20px;
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 44px;
    height: 44px;
    border-radius: 100%;
    padding: 10px;
    background-color: var(--light-color);
    transition: var(--transition);

    svg {
      width: 24px;
      height: 24px;
      fill: var(--accent-color);
    }

    &:focus {
      outline: none;
    }

    @include hover {
      background-color: var(--grey-color);
    }

    &:active {
      background-color: var(--grey-color);
    }
  }
}

@media screen and (max-width:1100px) {
  .nav {
    position: fixed;
    right: 0%;
    top: 0;
    z-index: 100;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    overflow: auto;
    padding: 120px 30px 50px;
    width: 40vw;
    height: 100vh;
    background-color: var(--light-color);
    visibility: hidden;
    transform: translateX(100%);
    transition: transform .2s ease-in-out, visibility .2s ease-in-out;

    &--visible {
      visibility: visible;
      transform: translateX(0);
    }

    &::before {
      content: "";
      position: absolute;
      right: 0;
      bottom: 0;
      z-index: -1;
      width: 100%;
      height: 100%;
      background-image: url("../img/menu.png");
      background-repeat: no-repeat;
      background-size: contain;
      background-position: bottom;
    }
  }

  .header {
    &__body {
      display: flex;
      justify-content: space-between;
    }

    &__list {
      flex-direction: column;
      justify-content: flex-start;
      height: 100%;
      gap: 30px;
      margin-bottom: 30px;
    }

    &__contact {
      flex-direction: column;
      width: 100%;

      &-item {
        width: 100%;

        .primary__btn {
          display: none;
        }
      }

      &-link {
        width: 100%;
        justify-content: center;
      }
    }

  }

  .mobile__nav {
    display: flex;
  }
}

@media screen and (max-width:767px) {
  .nav {
    width: 70vw;
  }
}

@media screen and (max-width:500px) {
  .nav {
    width: 100vw;
  }

  .header {
    &__logo {
      width: 55px;
      height: 55px;
    }
  }
}
