.contacts {
  padding: 60px 0;
  background-color: var(--white-color);

  &__container {
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 15px;
  }

  &__line {
    max-width: 330px;

    svg {
      stroke: #D9D9D9;
      fill: transparent;
    }
  }

  &__address {
    font-style: normal;
  }

  &__list {
    gap: 30px;
  }

  &__item {
    align-items: center;
    gap: 15px;

    &-link {
      display: flex;
      flex-direction: column;

      strong {
        font-size: 16px;
        font-weight: 600;
        line-height: 150%;
        color: var(--black-color);
      }

      span {
        font-size: 16px;
        font-weight: 400;
        line-height: 150%;
        color: var(--grey-color);
        transition: var(--transition);
      }

      &:focus {
        outline: none;
      }

      @include hover {
        span {
          color: var(--accent-color);
        }
      }

      &:active {
        span {
          color: var(--black-color);
        }
      }
    }

    svg {
      width: 50px;
      height: 50px;
      fill: transparent;
      stroke: var(--accent-color);
    }
  }
}

@media screen and (max-width:1024px) {
  .contacts {
    &__container {
      flex-direction: column;
    }
    &__line {
      width: 100%;
      max-width: 100%;
      svg {
        width: 100%;
        height: 100%;
      }
    }
  }
}

@media screen and (max-width:767px) {
  .contacts {
    padding: 30px 0;
    &__list {
      flex-direction: column;
      gap: 20px;
    }
  }
}
