.portfolio {
  &__container {
    align-items: center;
    justify-content: space-between;
    gap: 30px;
    margin-bottom: 60px;
  }

  &__title {
    position: relative;
    left: 50%;
    margin-bottom: 0;
    text-align: center;
    transform: translateX(-50%);
  }

  &__nav {
    align-items: center;
    gap: 15px;

    &-btn {
      display: none;
      justify-content: center;
      align-items: center;
      border: 1px solid var(--grey-color);
      border-radius: 100px;
      padding: 15px;
      width: 60px;
      height: 60px;
      transition: var(--transition);

      svg {
        display: block;
        width: 30px;
        height: 30px;
        stroke: var(--grey-color);
        transition: var(--transition);
      }

      &--prev {
        svg {
          transform: rotate(180deg);
        }
      }

      &:focus {
        outline: none;
      }

      @include hover {
        border-color: var(--accent-color);
        background-color: transparent;

        svg {
          stroke: var(--accent-color);
        }
      }

      &:active {
        border-color: var(--accent-color);
        background-color: var(--accent-color);

        svg {
          stroke: var(--white-color);
        }
      }
    }

  }

  &__slider {
    &-wrapper {
      margin: auto;
      margin-bottom: 30px;
      padding-left: max(15px, (100% - 1300px) / 2);
    }
  }
}


.video-container {
  position: relative;
  width: 100%;
  border-radius: 5px;
  height: 471px;
  background-color: red;
}

.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

.play-button {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  display: block;
  border: 1px solid var(--white-color);
  border-radius: 100%;
  width: 100px;
  height: 100px;
  background: rgba(252, 252, 252, 0.219);
  backdrop-filter: blur(2px);
  transform: translate(-50%, -50%);
  cursor: pointer;
  transition: all var(--transition);
  animation: pulse 3s infinite linear;

  span {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 2;
    width: 80px;
    height: 80px;
    background: var(--white-color);
    border-radius: 50%;
    background-image: url("../img/play.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30%;
    transform: translate(-50%, -50%);
  }
}

@keyframes pulse {
  0% {
    transform: scale(1) translate(-50%, -50%);
  }

  25% {
    transform: scale(1.05) translate(-50%, -50%);
  }

  50% {
    transform: scale(1) translate(-50%, -50%);
  }

  75% {
    transform: scale(1.05) translate(-50%, -50%);
  }

  100% {
    transform: scale(1) translate(-50%, -50%);
  }
}


.portfolio__custom-progressbar {
  display: block;
  width: 100% !important;
  height: 1px !important;
  background-color: #E2E2E2 !important;
  position: relative;
}

.portfolio__custom-progress {
  height: 100%;
  border-radius: 1px !important;
  background-color: #4D4E4F !important;
}

@media (min-width: 800px) {
  .portfolio__nav-btn {
    display: flex;
  }

  .portfolio__custom-progressbar {
    display: none;
  }

  .portfolio__slider-wrapper {
    margin-bottom: 0px;
  }
}


@media screen and (max-width:500px) {
  .portfolio {
    padding: 30px 0 75px;
    &__container {
      margin-bottom: 30px;
    }
  }
}
