.contact__form {
  position: relative;
  top: -50px;
  z-index: 3;

  &-wrapper {
    position: relative;
    border-radius: 5px;
    padding: 50px;
    background-color: var(--white-color);
    background-image: url("../img/obj.png");
    background-repeat: no-repeat;
    background-size: 600px;
    background-position: right;
  }

  &-title {
    position: relative;
    z-index: 2;
    margin-bottom: 30px;
    font-size: clamp(1.563rem, 1.384rem + 0.89vw, 2.188rem);
    font-weight: var(--semibold-font);
    line-height: 114%;
    color: var(--grey-color);
  }
}

.form {
  position: relative;
  z-index: 2;
  align-items: flex-end;
  justify-content: space-between;
  gap: 30px;

  &__list {
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }

  &__inputs {
    max-width: 723px;
  }

  &__field {
    &--full {
      grid-column: span 2;
      margin-bottom: 30px;
    }
  }

  &__label {
    display: block;
    margin-bottom: 15px;
    font-weight: var(--semibold-font);
    line-height: 150%;
    color: var(--black-color);
  }

  &__input {
    position: relative;
    z-index: 1;
    outline: none;
    display: block;
    border: 1px solid #E2E2E2;
    border-radius: 10px;
    padding: 15px 20px 15px 64px;
    min-width: 351px;
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 400;
    line-height: 150%;
    color: var(--black-color);
    background-color: var(--white-color);
    transition: .2s ease-in;

    &-wrapper {
      position: relative;
      z-index: 1;
      overflow: hidden;
    }

    &-icon {
      position: absolute;
      left: 20px;
      top: 50%;
      z-index: 2;
      width: 24px;
      height: 24px;
      fill: transparent;
      stroke: var(--grey-color);
      transform: translateY(-50%);
      transition: .2s ease-in;
    }

    &::placeholder {
      font-family: var(--font-family);
      font-size: 16px;
      font-weight: 400;
      line-height: 150%;
      color: var(--grey-color);
      transition: .2s ease-in;
    }

    &:focus {
      border: 1px solid var(--accent-color);
      padding: 15px 20px 15px 20px;

      &::placeholder {
        opacity: 0;
      }

    }

    &:not(:placeholder-shown) {
      padding: 15px 20px 15px 20px;
    }
  }

  &__textarea {
    resize: none;
    width: 100%;
    height: 141px;
    padding: 15px 20px;
  }

  &__btn {
    display: block;
    width: 100%;
    margin-bottom: 20px;

    @include hover {
      span {
        color: var(--white-color);

      }

      &::before {
        transform: translateX(0);
        background-color: var(--blue-color);
      }
    }

    &:active {
      &::before {
        background-color: var(--grey-color);
      }
    }
  }
}

.form__input-icon.hidden {
  transform: translate(-200%, -50%);
}

.custom-select {
  position: relative;
  z-index: 4;
  outline: none;
  display: block;
  border: 1px solid #E2E2E2;
  border-radius: 10px;
  padding: 15px 20px;
  min-width: 312px;
  font-family: var(--font-family);
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  color: var(--grey-color);
  background-color: var(--white-color);
  transition: .2s ease-in;
}

.custom-select select {
  display: none;
}

.select-selected:after {
  content: "";
  position: absolute;
  top: 50%;
  right: 20px;
  width: 20px;
  height: 20px;
  background-image: url("../img/arrow.svg");
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  transform: translateY(-50%);
  transition: transform .2s ease-in-out;
}

.select-selected.select-arrow-active:after {
  transform: translateY(-50%) rotate(180deg);
}

.select-items {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 25px 10px;
  border-radius: 5px;
  border: 1px solid var(--light-grey-color);
  background-color: var(--white-color);
  box-shadow: 0px 7px 7px -5px rgba(0, 0, 0, 0.04), 0px 10px 15px -5px rgba(0, 0, 0, 0.05), 0px 1px 3px 0px rgba(0, 0, 0, 0.05);
}

.select-hide {
  display: none;
}

.select-items div {
  font-weight: 400;
  padding: 8px 25px;
  color: var(--grey-color);
  transition: .2s ease-in;
}

.select-items div:hover,
.same-as-selected {
  padding: 8px 25px;
  border-radius: 10px;
  color: var(--accent-color);
  background-color: var(--light-grey-color, #FAFAFA);
}


.custom-checkbox {
  @include custom-checkbox;
  align-items: center;
  gap: 15px;

  &__field {
    border: 1px solid var(--grey-color);
    border-radius: 3px;
    width: 25px;
    height: 25px;
  }

  &__content {
    position: relative;
    padding-left: 40px;
    font-size: 12px;
    line-height: 150%;
    color: var(--grey-color);

    &::before {
      display: none;
    }

    &::after {
      border-radius: 3px;
      top: -4px;
      width: 25px;
      height: 25px;
      background-color: var(--accent-color);
      background-image: url("../img/check.svg");
      background-size: cover;
      background-repeat: no-repeat;
      background-position: 50% 50%;
    }
  }
}

@media screen and (max-width:1024px) {
  .contact__form {
    &-wrapper {
      padding: 30px;
    }
  }

  .form__input,
  .custom-select {
    min-width: 50%;
  }
}

@media screen and (max-width:850px) {
  .first__form {
    flex-direction: column;
    align-items: center;
  }

  .form {
    &__list {
      grid-template-columns: 1fr;
      width: 100%;
    }

    &__inputs {
      max-width: 100%;
      width: 100%;
    }

    &__input {
      min-width: 100%;
    }
    &__nav {
      width: 100%;
    }
  }

  .contact__form {
    top: -100px;
    margin-bottom: -50px;
  }
}
